body {
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  padding: 40px;
}

.card{
  margin: 20px;
  padding: 20px;
  border-radius: 6px;
  box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
}

.card .map-frame{
  width: 100%;
}

.card .table-frame .dataTables_wrapper{
  width: 100%;
}

.card .card-content{
  display: flex;
}

.chart-container{
  display: flex;
}

.chart-container .line-chart{
  width: 50%;
}

/*General Use Classes*/
.flex-row{
  display: flex;
  flex-direction: row;
}
.map-circle {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 40px;
    height: 40px;
    border-radius: 20px;
    background-color: rgba(255,0,0,0.5);
    border: 2px solid rgba(255,0,0,1);
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}

.value{
    position: absolute;
    background-color: #fff;
    border-radius: 3px;
    padding: 6px 6px;
}
.map-wrapper{
    width: 60%;
    position: relative;
    height: 540px;
}

.checkbox-wrapper{
    padding: 20px 40px;
}
